* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.content {
    height: 80vh;
}

.register-page {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
    background-color: pink;
}

.register {
    background-color: rgb(155, 187, 187);
    border: 3px solid black;
    border-radius: 15px;
    /* box-shadow: 2px 2px 2px 10px rgb(215, 175, 215); */
    padding: 35px;
}


/* Transection page */

.filters {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    box-shadow: 0 0 3px gray;
}